import { throttle } from 'lodash-es';

type ThrottleSettings = Parameters<typeof throttle>[2];

function Throttle(wait?: number, options?: ThrottleSettings): MethodDecorator {
  return function deco<Function>(target: object, propertyKey: string | symbol, descriptor: TypedPropertyDescriptor<Function>): TypedPropertyDescriptor<Function> {
    /* eslint-disable @typescript-eslint/no-explicit-any */
    const value = throttle(descriptor.value as any, wait, options) as any;
    return {
      ...descriptor,
      value,
    };
  };
}

export default Throttle;
